.POC {
  background-color: #1C1C1C;
  display: flex;
  justify-content: center;
  height: 100vh;
}

.phone-wrap {
  width: 100%;
  max-width: 480px;
  padding: 32px 0;
  position: relative;
}

.info-middle {
  margin: 32px 10% 128px;
  color: #FAFAFA;
}

.info-middle > div {
  margin: 36px 0;
}

.popup {
  width: 100%;
  height: 100%;
  background-color: #0000002d;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -48px;
  opacity: 0;
  /* visibility: hidden; */
  transition: 0.3s ease all;
  transition-property: opacity;
  pointer-events: none;
}

.popup.show {
  /* visibility: visible; */
  opacity: 1;
  pointer-events: inherit;
}

.popup-inner {
  position: relative;
}

.popup-inner > .x-icon {
  position: absolute;
  top: 18px;
  right: 18px;
  cursor: pointer;
}

.popup-inner > .btn {
  position: absolute;
  top: 222px;
  left: 76px;
  cursor: pointer;
}


.title {
  display: flex;
  justify-content: center;
}

.title > img {
  width: 80%;
  max-width: 292px;
}

.buy-btn > img, .mail-btn > img, .connect-btn > img {
  height: 30vw;
  max-height: 168px;
}

.connect-btn {
  display: flex;
  justify-content: center;
}

.connect-section {
  color: #f9f9f9;
  text-align: center;
}

.connect-section > * {
  margin: 48px 0;
}

.btn {
  transition: 0.2s ease all;
  transition-property: scale;
}

.btn:hover {
  cursor: pointer;
  scale: 1.02;
}

.btn:active {
  scale: 0.98;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: space-around;
  width: 80%;
  margin-left: 10%;
}

.top-btns {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 32px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
  font-family: Arial, sans-serif;
}
th {
  border: 1px solid #ddd;
  padding: 8px;
  background-color: #80B4E5;
  color: white;
  text-align: left;
}

td {
  border: 1px solid #ddd;
  padding: 8px;
  font-family: monospace;
}

.evenRow {
  background-color: #f9f9f9;
}

.oddRow {
  background-color: #fff;
}